@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    color: white;
    display: flex;
    align-items: center;
    background: #000;
    height: 70px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
}
.header__left{
    flex: 0.4 1;
}
.header__left h1{
    text-align: center;
    font-family: 'Alata';
    letter-spacing: 1px;
}
.header__left h1 span{
    color: orangered;
}
.header__right{
    display: flex;
    flex: 0.6 1;
    justify-content: space-evenly;
    align-items: center; 
}
.header__right  h4{
    font-size: 20px;
    cursor: pointer;
    transition:-webkit-transform ease-in-out;
    transition:transform ease-in-out;
    transition:transform ease-in-out, -webkit-transform ease-in-out;
}
.header__right a h4:hover{
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
    background-color: orangered;
    color: aliceblue; 
    cursor: pointer;
    border-radius: 5px;
    font-family: "Crimson Text";
    padding: 7.3px 20px;
}
.button{
    background-color: orangered;
    padding: 5px 20px;
    border-radius: 5px;
    cursor: pointer;
}
.content{
    color: aliceblue;
}

.container{
    position: absolute;
    top: 200px;
    right: 150px;
}
.container p{
    letter-spacing: 1px;
    text-align: left;
    padding: 10px 0 10px 0;
    margin-left: 5px;
    font-size: 14px;
}
.container a button{
    border-radius: 5px;
    padding: 5px;
    margin: 10px 0 0px 5px;
    border: none;
}
.container h1{
    font-family: "Montserrat",Courier, monospace;
    font-size: 50px;
}
.container .button_left{
    font-family: "Montserrat",Courier, monospace;
    font-size: 15px;
    padding: 10px 30px;
    background-color: orangered;
    color: aliceblue;
    font-weight: 300;
    margin-right: 30px;
}
.container .button_left:hover{
    color: orangered;
    background: white;
    font-weight: 600;
}
.container .button_right{
    font-family: "Montserrat",Courier, monospace;
    font-size: 15px;
    padding: 10px 30px;
    background-color: transparent;
    color: aliceblue;
    font-weight: 300;
    margin-right: 30px;
    border: 1px solid white;
}
.container .button_right:hover{
    color: black;
    background: white;
    font-weight: 600;
}
.top{
    background: url(/Resume/static/media/1.e3a641a3.jpeg);
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 500px rgba(0, 0, 0, 0.4);
}
.skillname{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 100px 0;
}
.skilltext{
    flex: 0.6 1;
    color: rgb(255, 255, 255);
    text-align: left;
    margin-bottom: 20px;
    max-width: 500px;
}
.skilltext h2{
    font-size: xx-large;
}
.skillimage {
    flex: 0.4 1;
    margin-right: 40px;
}
.skillimage img{
    width: 400px;
    border-radius: 4px;
}
.skillsets{
    margin-top: 15px;
}
.skill_silder1 .MuiLinearProgress-barColorPrimary{
    background: rgb(0, 26, 255);
}
.skill_silder2 .MuiLinearProgress-barColorPrimary{
    background: rgb(231, 3, 148);
}
.skill_silder3 .MuiLinearProgress-barColorPrimary{
    background: rgb(250, 0, 0);
}
.skill_silder4 .MuiLinearProgress-barColorPrimary{
    background: rgb(9, 255, 0);
}
.skill_silder5 .MuiLinearProgress-barColorPrimary{
    background: rgb(0, 204, 255);
}
.projectContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.projectContainer h3{
    color: aliceblue;
    font-size: x-large;
}
.projectContainer p{
    color: aliceblue;
    font-size: large;
}
.projectContainer_projects{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 80vw;
}
.project img{
    width: 300px;
    border-radius: 30px;
}
.project{
    width: 300px;
    margin: 15px;
}
.project:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: orangered;
    color: aliceblue;
    border-radius: 30px;
}
.pro h3{
    padding-bottom: 10px;
    margin: 15px;
    font-size: xx-large;
}
.pro p{
    margin: 15px;
    font-size: medium;
}
a{
    text-decoration: none;
}
.ex{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 200px 0;
    color: aliceblue;
}
.ex h1{
    padding-bottom: 20px;
}
.exper{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 300px;
    width: 80vw;
    z-index: 1000px;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.9
    );
}
.experiment{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px dotted white;
    flex-direction: column;
    border-radius: 10px;
    overflow: hidden;
    background: #000;
    width: 200px;
    height: 150px;

}
.experiment p{
    font-size: 16px;
    letter-spacing: 1px;
}
.contact{
    color: antiquewhite;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.p{
    border: 2px solid red;
    border-radius: 30px;
    padding: 30px;
    width: 500px;
    height: 200px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.p p{
    padding-bottom: 20px;
    font-size: x-large;
}

.p p span{
    color: orangered;
}
.icons a button span .MuiSvgIcon-root{
    color: rgb(255, 255, 255);
    font-size: 2rem;
}
